/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

/* Global Variables */
:root {
  --primary-color: #00e676;
  --primary-dark: #00c853;
  --background-dark: #121212;
  --background-darker: #0a0a0a;
  --background-card: #1e1e1e;
  --text-primary: #ffffff;
  --text-secondary: #b3b3b3;
  --text-muted: #757575;
  --border-color: #333333;
  --success: #00e676;
  --warning: #ff9800;
  --error: #f44336;
  --shadow: 0 4px 12px rgba(0, 0, 0, 0.25);
}

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background-color: var(--background-dark);
  color: var(--text-primary);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Container */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Header */
.App-header {
  background-color: var(--background-darker);
  padding: 1rem 0;
  border-bottom: 1px solid var(--border-color);
  position: sticky;
  top: 0;
  z-index: 100;
}

.App-header nav ul {
  display: flex;
  list-style: none;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.App-header nav ul li a {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
  padding: 0.5rem 1rem;
  border-radius: 4px;
}

.App-header nav ul li a:hover {
  color: var(--primary-color);
  background-color: rgba(0, 230, 118, 0.1);
}

/* Main Content */
.App-main {
  flex: 1;
  padding: 2rem 0;
}

/* Homepage Styles */
.homepage {
  background-color: var(--background-dark);
  min-height: 100vh;
}

/* Header Section */
.header {
  background-color: var(--background-darker);
  padding: 1rem 0;
  border-bottom: 1px solid var(--border-color);
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--primary-color);
  letter-spacing: -0.5px;
}

.nav ul {
  display: flex;
  list-style: none;
  gap: 1.5rem;
}

.nav ul li a {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.nav ul li a:hover {
  color: var(--primary-color);
}

.user-actions {
  display: flex;
  gap: 1rem;
}

.login-btn, .signup-btn {
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
  font-family: 'Inter', sans-serif;
}

.login-btn {
  background-color: transparent;
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.login-btn:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.signup-btn {
  background-color: var(--primary-color);
  color: var(--background-dark);
  border: 1px solid var(--primary-color);
}

.signup-btn:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

/* Hero Section */
.hero-section {
  padding: 4rem 0;
  text-align: center;
  background: linear-gradient(135deg, rgba(0, 230, 118, 0.1) 0%, rgba(0, 0, 0, 0) 100%);
}

.hero-section h2 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #ffffff, var(--primary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-section p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto 2rem;
}

.explore-btn {
  background-color: var(--primary-color);
  color: var(--background-dark);
  border: none;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Inter', sans-serif;
}

.explore-btn:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

/* Summer Promo Section */
.summer-promo {
  background-color: var(--background-darker);
  padding: 3rem 0;
  text-align: center;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
}

.summer-promo h3 {
  color: var(--primary-color);
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.summer-promo p {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

/* Path Start Section */
.path-start-section {
  padding: 3rem 0;
  text-align: center;
}

.path-start-section h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.path-start-section p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.path-options {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.path-options label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.path-options label:hover {
  border-color: var(--primary-color);
  background-color: rgba(0, 230, 118, 0.05);
}

.path-options input[type="radio"] {
  accent-color: var(--primary-color);
}

/* Placeholder Sections */
.most-popular-section,
.upcoming-section,
.open-contests-section,
.past-winners-section,
.partners-section,
.membership-section,
.user-say-section,
.faq-section {
  padding: 3rem 0;
  border-top: 1px solid var(--border-color);
}

.most-popular-section h3,
.upcoming-section h3,
.open-contests-section h3,
.past-winners-section h3,
.partners-section h3,
.membership-section h3,
.user-say-section h3,
.faq-section h3 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Footer */
.footer {
  background-color: var(--background-darker);
  padding: 2rem 0;
  border-top: 1px solid var(--border-color);
  text-align: center;
}

/* Login Page Styles */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  padding: 2rem;
}

.login-container {
  background-color: var(--background-card);
  border-radius: 8px;
  padding: 2.5rem;
  width: 100%;
  max-width: 450px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
}

.login-logo {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 2rem;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 500;
  color: var(--text-primary);
}

.form-group input {
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid var(--border-color);
  background-color: var(--background-dark);
  color: var(--text-primary);
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
}

.form-group input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(0, 230, 118, 0.2);
}

.password-input-wrapper {
  position: relative;
}

.password-toggle {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: var(--text-muted);
}

.login-btn {
  background-color: var(--primary-color);
  color: var(--background-dark);
  border: none;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: 'Inter', sans-serif;
  margin-top: 0.5rem;
}

.login-btn:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.forgot-password-link {
  text-align: center;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.forgot-password-link:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .header .container {
    flex-direction: column;
    text-align: center;
  }
  
  .nav ul {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .user-actions {
    justify-content: center;
  }
  
  .hero-section h2 {
    font-size: 2rem;
  }
  
  .hero-section p {
    font-size: 1rem;
  }
  
  .path-options {
    flex-direction: column;
    align-items: center;
  }
  
  .login-container {
    padding: 1.5rem;
  }
}